.App{
  min-height: 100vh;
  background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);
}

.search-form{
  min-height:10vh ;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  width: 50%;
  border: none;
  padding: 10px;
}
.search-button{
  background: rgb(69, 9, 159);
  border: none;
  padding: 10px 20px;
  color: white;
}
.recipe{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.appname{
  color: teal;
  text-align: center;
  font-size: 50px;
}